import axios from 'axios';
import React, {useState} from 'react';
import {useHistory} from 'react-router-dom';

const Add_Data = () => {
    let history=useHistory();
    const[user,setuser]=useState({
        company:"",
        email:"",
        address:"",
        ename:"",
        designation:"",
        qualification:""
    });
    const{company, email, address, ename, designation, qualification} = user;
    const onInputChange=(e)=>{
        setuser({...user,[e.target.name]:e.target.value})
    }
    const onSubmit= async e =>{
        e.preventDefault();
        await axios.post("http://localhost:3002/users",user);
        history.push("/");
    }
    return (

        <div className="container">
            <div className="row">
                <div className="col-md-12 text-center pt-3">
                    <h1>Add Company Details</h1>
                    </div>
                    </div>
                    <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-6 text-center">
                    <form onSubmit={e=>onSubmit(e)} className="pt-2">
                        <div className="mb-3 bg-primary p-1">
                            <input name="company" value={company} onChange={e=>onInputChange(e)} className="form-control col-12" type="text" placeholder="Enter Company Name" readonly/>
                        </div>
                        <div className="mb-3 bg-primary p-1">
                            <input name="email" value={email} onChange={e=>onInputChange(e)} className="form-control col-12" type="email" placeholder="Enter Company Email" readonly/>
                        </div>
                        <div className="mb-3 bg-primary p-1">
                            <input name="address" value={address} onChange={e=>onInputChange(e)} className="form-control col-12" type="text" placeholder="Enter Company Address" readonly/>
                        </div>
                        <div className="mb-3 bg-primary p-1">
                            <input name="ename" value={ename} onChange={e=>onInputChange(e)} className="form-control col-12" type="text" placeholder="Enter Employee Name" readonly/>
                        </div>
                        <div className="mb-3 bg-primary p-1">
                            <input name="designation" value={designation} onChange={e=>onInputChange(e)} className="form-control col-12" type="text" placeholder="Enter Employee Designation" readonly/>
                        </div>
                        <div className="mb-3 bg-primary p-1">
                            <input name="qualification" value={qualification} onChange={e=>onInputChange(e)} className="form-control col-12" type="text" placeholder="Enter Employee Qualification" readonly/>
                        </div>
                        <button type="submit" className="btn btn-primary">Submit</button>
                    </form>
                    </div>
                    <div className="col-md-3">
                    </div>
                </div>
            </div>
    )
}
export default Add_Data;