import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const Home = () => {
    const [users, setuser] = useState([]);

    useEffect(() => {
        loadusers();
    }, [])

    const loadusers = async () => {
        const result = await axios.get("http://localhost:3002/users");
        setuser(result.data.reverse());
    }
    const deleteUser = async id =>{
        alert("Are You Sure?");
        await axios.delete(`http://localhost:3002/users/${id}`);
        loadusers();
    }

    return (
        <div className="container">
            <div className="py-4">
                <h1>Home Page</h1>
                <h3>Total Data: {users.length}</h3>
                <table className="table border shadow">
                    <thead className="table-light">
                        <tr>
                            <th>ID</th>
                            <th>Company Name</th>
                            <th>Email Id</th>
                            <th>Address</th>
                            <th>Employee Name</th>
                            <th>Designation</th>
                            <th>Qualification</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            users.map((xuser, index)=>{
                                return(
                                    <tr key={index}>
                                        <td>{index+1}</td>
                                        <td>{xuser.company}</td>
                                        <td>{xuser.email}</td>
                                        <td>{xuser.address}</td>
                                        <td>{xuser.ename}</td>
                                        <td>{xuser.designation}</td>
                                        <td>{xuser.qualification}</td>
                                        <td>
                                            <Link to={`/edituser/${xuser.id}`} className="btn btn-primary btn-sm m-1">Edit</Link>
                                            <Link className="btn btn-danger btn-sm" onClick={()=>deleteUser(xuser.id)}>Delete</Link>
                                        </td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}
export default Home;